/*--------------------_programms-page----------------------*/
.page {}
.page-programms {

}
.wrapper-simple {}
.programms-cards-slider{
	// @include max-screen($sm){
	// 	margin-left: 10px;
	// }
	@include max-screen($sm){
		margin-bottom: 30px;
	}

	&.slick-dotted{
		@include max-screen($sm){
			margin-bottom: 30px;
		}
	}
	.slick-list{
		@include max-screen($sm){
			padding:0 10% 0 0 !important;
			width: calc(100% + 15px);
		}
	}
	.slick-track{
		display: flex;
		flex-wrap: wrap;
		@include min-screen($sm){
			width: 100% !important;
			transform: translate3d(0px, 0px, 0px) !important;
		}
		@include min-screen($md){
			width: calc(100% + 30px) !important;
			margin-left: -15px;
			margin-right: -15px;
			// margin-left: 0;
			// margin-right: 0;
		}
	}

	.slick-slide{
		display: flex;
		float: none;
		height: auto;

		&.slick-cloned{
			@include min-screen($sm){
				display: none;
				
			}
		}
	}
	.programm-card {
    	display: flex;

    	@include min-screen($sm){
    		margin-bottom: 30px;
    		width: 100% !important;
    		margin-left: 0;
    		margin-right: 0;
    		
    	}
    	@include min-screen($md){
    		width: calc(50% - 30px) !important;
    		margin-left: 15px;
    		margin-right: 15px;
    	}
    	@include max-screen($sm){
    		margin-left: 0;
    		margin-right: 30px;   		
    	}
    	@include max-screen($xss){
    		flex-direction: column;
    	}
	}
	.programm-card__img{
		// height: 280px;
		@include max-screen($xss){
			flex: 1 1 auto;
			height: 280px;
		}
	}
	.program-icon{
		@include max-screen($xss){
			right: auto;
			left: 50%;
			bottom: -24px;
			transform: translateX(-50%);
			
		}
	}
	.programm-card__content{
		@include max-screen($xss){
			padding-bottom: 30px;
		}
	}
	.slick-dots{
		margin-top: 10px;
		text-align: left;
	}
}
.programms-choice-slider{

	&.slick-dotted{
		margin-bottom: 20px;

	}
	.slick-list{
		@include max-screen($xs){
			padding-right: 10% !important;
			
		}
		@include max-screen($sm){
			margin-left: -15px;
			margin-right: -15px;

		}
		@include min-screen($xs){
			margin-bottom: 20px;
			
		}
		@include min-screen($sm){
			margin-bottom: 20px;
			margin-bottom: 0;
		}  
	}

	.slick-track{

			display: flex;
			flex-wrap: wrap;
		@include min-screen($sm){
			margin-left: -15px;
			margin-right: -15px;
			width: calc(100% + 30px) !important;
			transform: translate3d(0px, 0px, 0px) !important;
		}
	}

	.slick-slide{
		margin-left: 15px;
		margin-right: 15px;
		height: auto;
		float: none;

		@include min-screen($sm){
			max-width: calc(50% - 30px);
			width: calc(50% - 30px) !important;
		}
		@include min-screen(1100px){
			max-width: calc(33.33333% - 30px);
			width: calc(33.33333% - 30px) !important;

		}
		@include min-screen($sm){
			margin-bottom: 20px;
		}
		@include max-screen($sm){
			max-width: none;
			// margin-left: 0;
			// margin-right: 30px;
		}
	}

	.slick-cloned{
		@include min-screen($sm){

			display: none !important;
		}
	}
	.slick-dots{
		margin-top: 10px;
		text-align: left;
	}
}
.programms-filters {
	@include col();
	flex: 1 1 170px;

	@include max-screen($sm){
		display: none;
	}
}
.programms-content {
	@include col();
	width: calc(100% - 170px - 60px);
	flex-shrink: 0;

	@include max-screen($sm){
		width: calc(100% - 30px);
	}
}
.programms-content__block {

	&:not(:last-child){
		margin-bottom: 30px;
	}
	&.expanded{
		.programms-cards-slider{
			&.slick-dotted{
				margin-bottom: 0;
			}
			.slick-list{
				// height: auto !important;
				padding: 0 0 0 0 !important;
			}
			.slick-track{
				margin-left: 0;
				margin-right: 15px;
				width: calc(100% + 15px) !important;
				transform: translate3d(0px, 0px, 0px) !important;

				@include min-screen($md){
	    		margin-right: -15px;
	    		margin-left: -15px;
	    	}

				.slick-slide{
					display: flex;
					float: none;
					height: auto;
				}

				.programm-card {
			    	display: flex;
		    		width: calc(100% - 30px) !important;

			    	@include min-screen($sm){
			    		margin-bottom: 30px;
			    		width: 100% !important;
			    		
			    	}
			    	@include min-screen($md){
			    		width: calc(50% - 30px) !important;
			    		margin-right: 15px !important;
			    		margin-left: 15px;
			    	}
			    	@include max-screen($sm){
			    		margin-left: 0;
			    		margin-right: 0;   		
			    	}

				}

				.slick-slide{
					margin-right: 0;
					&:not(:last-of-type){
						margin-bottom: 30px;
					}
					&.slick-cloned{
						display: none !important;
					}
				}
			}
			.slick-dots{
				display: none !important;
			}
		}
		.programms-choice-slider{
			&.slick-dotted{
				margin-bottom: 0;
			}
			.slick-list{
				padding-right: 0 !important;
			}
			.slick-track{
				// margin-left: -15px;
				// margin-right: -15px;
				display: flex;
				flex-wrap: wrap;
				width: 100% !important;
				transform: translate3d(0px, 0px, 0px) !important;
	
				.slick-slide{
						margin-bottom: 30px;

					@include max-screen($sm){
						max-width: calc(50% - 30px);
						width: calc(50% - 30px) !important;
					}  
					@include max-screen($xs){
						max-width: 100% !important;
						width: 100% !important;
						display: block;
					}  

					&.slick-cloned{
						display: none;
					}
				}
			}

			.slick-dots{
				display: none !important;
			}
		}

		.btn{
			display: none;
		}
	}
	.btn{
		@include min-screen($sm){
			display: none;
		}
	}
}
.programms-filters__title {
	margin-bottom: 20px;
}
.form {}
.programms-filters__block {
	margin-top: 30px;

}
.programms-filters__heading {
	margin-bottom: 15px;
	font-family: $bold;
	font-size: 16px;
line-height: 20px;
}
.form__label {
	&:not(:last-child){
		margin-bottom: 17px;
		
	}
}
.form__checkbox {}
.form__false-checkbox {
	margin-right: 9px;
}
.icon {}
.icon-check {}
